<template>
  <teleport to="body">
    <div
        v-if="visible"
        class="fixed z-[9999] bg-white text-dark border rounded shadow-lg p-3 text-sm max-w-sm overflow-auto max-h-[90vh]"
        :style="{ top: position.top, left: position.left }"
    >
      <div class="font-mono whitespace-pre-wrap text-xs">
        <div v-for="(value, key) in content" :key="key">
          <template v-if="typeof value === 'object' && value !== null">
            <div><strong>{{ key }}:</strong></div>
            <div v-for="(subValue, subKey) in value" :key="subKey" class="pl-4">
              <strong>{{ subKey }}:</strong> {{ subValue }}
            </div>
          </template>
          <template v-else>
            <div><strong>{{ key }}:</strong> {{ value }}</div>
          </template>
        </div>
      </div>
    </div>
  </teleport>
</template>
<script>
export default {
  name: 'InfoOverlay',
  props: {
    visible: Boolean,
    content: String,
    position: Object,
  },
};
</script>
